.bottom-bar {
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
    border-top: 1px solid #ddd;
}

.bottom-bar .range-control-range { background-color: #ccc; margin-right: 10px; padding-left: 2px; padding-right: 2px}
.bottom-bar .range-marker { color: #fff}
.pipe-wrapper { display: inline-flex; width: 100% }
.pipe-wrapper .pipe-info { width: 100% }
.pipe-row{ display: flex; justify-content: space-between; margin-left: 10px;}
.pipe-row.inline {
    justify-content: normal;
}
.dep-control:hover {
    background-color: #dddddd;
}

.create-action {
    visibility: hidden;
}
.dep-control:hover .create-action {
    visibility: visible;
}

.hints {
    margin-top: calc(100vh - 300px);
}

.ident-1 {margin-left: 10px}
.ident-2 {margin-left: 20px}
.ident-3 {margin-left: 30px}
.ident-4 {margin-left: 40px}
.ident-5 {margin-left: 50px}
.ident-6 {margin-left: 60px}
.secondary-flow {opacity: 0.5}

.separator {
    background-color: red;
}
